import * as d3 from "d3";
import {
  MAP_LEFT,
  MAP_RIGHT,
  MAP_UP,
  MAP_DOWN,
  MAP_CENTER,
  generateArrow,
  kde_2d_gpu,
  kde_2d_cpu,
} from "./globals";
import {getChinaMapJSON, getFilteredElites} from "./data";


//===========================================================================
//                       KDE  Contour 
//===========================================================================
function drawContour(svg, year, type, kernel_width, grid_size, use_gpu) {

  //  (x, y) 
  let filteredElites = getFilteredElites({type:type, year:year, useLiteData:!use_gpu})    //  cpu 
  let data = filteredElites.map((d)=>d.getPos(year))

  // SVG 
  let width = svg.attr("width")
  let height = svg.attr("height")

  // (x, y)  (i,j) 
  let xScale = d3.scaleLinear()
    .domain([0, grid_size])
    .range([MAP_LEFT, MAP_RIGHT])
  let yScale = d3.scaleLinear()
    .domain([0, grid_size])
    .range([MAP_DOWN, MAP_UP])

  // 
  let samples = Array(grid_size ** 2)
  for (let i = 0; i < grid_size; i++) {
    for (let j = 0; j < grid_size; j++) {
      let x = xScale(j)   //  i, j  y, x!
      let y = yScale(i)
      samples[i * grid_size + j] = [x, y]
    }
  }

  // 
  let kde_2d = use_gpu ? kde_2d_gpu : kde_2d_cpu      //  CPU or GPU 
  let values = kde_2d(kernel_width, samples, data);

  //  values  contour 
  let contours = d3.contours()
    .size([grid_size, grid_size])
    .thresholds(40)(values);      // Contour .

  const svg2 =
    svg.append("svg")
      .attr("width", width)
      .attr("height", height)
      .attr("viewBox", `0, 0, ${grid_size}, ${grid_size}`)
      .attr("preserveAspectRatio", "none")

  const color = d3.scaleSequential(d3.interpolateTurbo)
    .domain(d3.extent(values))

  svg2.append("g")
    .attr("fill", "none")
    .selectAll("path")
    .data(contours)         //  contour 
    .enter()
    .append("path")
    .attr("d", d3.geoPath())
    .style("fill", d => color(d.value))
  return svg2.node();
}


//===========================================================================
//                              
//===========================================================================
function drawChinaMap(svg) {

  // 
  let canvas_height = svg.attr("height");
  let canvas_width = svg.attr("width");

  // 
  let scale = canvas_height * 1.09;
  let projection =
    d3.geoMercator()
      .center(MAP_CENTER)                      //  [] 
      .translate([canvas_width / 2, canvas_height / 2])       // 
      .scale(scale)                             //

  // , , 
  let path = d3.geoPath()
    .projection(projection);    // 

  // 
  svg.append("g")
    .selectAll("path")
    .data(getChinaMapJSON().features)      // 
    .enter()
    .append("path")
    .style("fill", '#fff0')    //
    .style("stroke", "#aaa")
    .attr("d", path)          //

  // 
  let xScale = d3.scaleLinear()
    .domain([MAP_LEFT, MAP_RIGHT])
    .range([0, canvas_width])
  let xAxis = d3.axisBottom()
    .scale(xScale)
    .tickValues([80, 85, 90, 95, 100, 105, 110, 115, 120, 125, 130]);
  svg.append("g")
    .call(xAxis)
    .attr("color", "#aaa")
    .call(g => g.select(".domain").remove())

  // 
  let yScale = d3.scaleLinear()
    .domain([MAP_DOWN, MAP_UP])
    .range([0, canvas_height])
  let yAxis = d3.axisRight()
    .scale(yScale)
    .tickValues([15, 20, 25, 30, 35, 40, 45, 50, 55]);
  svg.append("g")
    .call(yAxis)
    .call(g => g.select(".domain").remove())
    .attr("color", "#aaa")

  // 
  svg.attr("transform", "translate(-1, -1)")

}


//===========================================================================
//                              
//===========================================================================
function drawScatter(svg, year, type, use_lite_data) {

  //  (x, y) 
  let filteredElites = getFilteredElites({type:type, year:year, useLiteData:use_lite_data})
  let data = filteredElites.map((d) => d.getPos(year));

  // 
  let width = svg.attr("width")
  let height = svg.attr("height")

  // 
  let x = d3.scaleLinear()
    .domain([MAP_LEFT, MAP_RIGHT])
    .range([0, width])
  let y = d3.scaleLinear()
    .domain([MAP_DOWN, MAP_UP])
    .range([0, height])

  // 
  svg.append("g")
    .selectAll("g")
    .data(data)
    .join("g")
    .append('circle')
    .attr('cx', d=>x(d[0]))
    .attr('cy', d=>y(d[1]))
    .attr('r', 1)
    .attr("fill", "red")

}


//===========================================================================
//                              
//===========================================================================
function drawArrow(svg, center_x, center_y, radius, angle) {

  // 
  let width = svg.attr("width")
  let height = svg.attr("height")

  // 
  let theta = angle / 360 * 2 * Math.PI

  // (x, y)  (i,j)
  let xScale = d3.scaleLinear()
    .domain([MAP_LEFT, MAP_RIGHT])
    .range([0, width])
  let yScale = d3.scaleLinear()
    .domain([MAP_DOWN, MAP_UP])
    .range([0, height])

  //  point_1  point_2 (!!!!!)
  let x1 = Number(xScale(center_x - radius * Math.cos(theta)))
  let y1 = Number(yScale(center_y - radius * Math.sin(theta)))
  let x2 = Number(xScale(center_x + radius * Math.cos(theta)))
  let y2 = Number(yScale(center_y + radius * Math.sin(theta)))


  //  ()
  let g = svg.append("g")

  // 
  let head_size = width * 0.015;
  g.append('path')
    .attr('d', generateArrow(x1, y1, x2, y2, head_size))
    .style('fill', 'none')
    .style('stroke', '#aa9cff')
    .style("stroke-width", 4)
  g.append('path')
    .attr('d', generateArrow(x1, y1, x2, y2, head_size - 1))
    .style('fill', 'none')
    .style('stroke', '#fff')
    .style("stroke-width", 1.5)

  // 
  g.append('circle')
    .attr('cx', (x1 + x2) / 2)
    .attr('cy', (y1 + y2) / 2)
    .attr('r', 5)
    .style('fill', '#aa9cff')
  g.append('circle')
    .attr('cx', (x1 + x2) / 2)
    .attr('cy', (y1 + y2) / 2)
    .attr('r', 3.75)
    .style('fill', '#fff')

  // 
  g.append('circle')
    .attr('cx', x1)
    .attr('cy', y1)
    .attr('r', 5)
    .style('fill', '#aa9cff')
  g.append('circle')
    .attr('cx', x1)
    .attr('cy', y1)
    .attr('r', 3.75)
    .style('fill', '#fff')

  g.attr("id", "arrow")
}


//===========================================================================
//                              
//===========================================================================
function drawToggleScatterButton(svg, showing_scatter, toggle_state_func) {
  // 
  let width = svg.attr("width")
  let height = svg.attr("height")

  // 
  let g = svg.append("g")
  g.append("rect")
    .attr("fill", "#2b2b2c")
    .attr("stroke", "#5d5dff")
    .attr("x", 0)
    .attr("y", 0)
    .attr("width", 0.175 * width)
    .attr("height", 0.035 * height)
    .attr("rx", 5)
    .attr("ry", 5)

  // 
  g.append("text")
    .text(showing_scatter ? "Hide Scatter Plot" : "Show Scatter Plot")
    .attr("fill", "#fff")
    .attr("font-size", "0.5em")
    .attr("x", height * 0.015)
    .attr("y", height * 0.025)

  //  + 
  g.attr("transform", `translate(${width * 0.05}, ${0.05 * height})`)
    .on("click", toggle_state_func)
    .style("cursor", "pointer")
}


//===========================================================================
//                              
//===========================================================================
function drawToggleContourButton(svg, showing_contour, toggle_state_func) {
  // 
  let width = svg.attr("width")
  let height = svg.attr("height")

  // 
  let g = svg.append("g")
  g.append("rect")
    .attr("fill", "#2b2b2c")
    .attr("stroke", "#5d5dff")
    .attr("x", 0)
    .attr("y", 0)
    .attr("width", 0.155 * width)
    .attr("height", 0.035 * height)
    .attr("rx", 5)
    .attr("ry", 5)

  // 
  g.append("text")
    .text(showing_contour ? "Hide HeatMap" : "Show HeatMap")
    .attr("fill", "#fff")
    .attr("font-size", "0.5em")
    .attr("x", height * 0.015)
    .attr("y", height * 0.025)

  //  + 
  g.attr("transform", `translate(${width * 0.05}, ${0.05 * height + 1 * 0.05 * height})`)
    .on("click", toggle_state_func)
    .style("cursor", "pointer")
}



//===========================================================================
//                           CPU / GPU 
//===========================================================================
function drawToggleCPUGPU(svg, now_using_gpu, toggle_state_func) {
  // 
  let width = svg.attr("width")
  let height = svg.attr("height")

  // 
  let g = svg.append("g")
  g.append("rect")
    .attr("fill", "#2b2b2c")
    .attr("stroke", "#5d5dff")
    .attr("x", 0)
    .attr("y", 0)
    .attr("width", 0.145 * width)
    .attr("height", 0.035 * height)
    .attr("rx", 5)
    .attr("ry", 5)

  // 
  g.append("text")
    .text(now_using_gpu ? "Current: GPU" : "Current: CPU")
    .attr("fill", "#fff")
    .attr("font-size", "0.5em")
    .attr("x", height * 0.015)
    .attr("y", height * 0.025)

  //  + 
  g.attr("transform", `translate(${width * 0.05}, ${0.05 * height + 2 * 0.05 * height})`)
    .on("click", toggle_state_func)
    .style("cursor", "pointer")
}




export {
  drawContour,
  drawScatter,
  drawArrow,
  drawChinaMap,
  drawToggleScatterButton,
  drawToggleContourButton,
  drawToggleCPUGPU
}